  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
}
html {
  font-family: 'sans-serif', "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", "Tahoma", "Helvetica";
  font-size: 10px;
  background: #fff;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0)
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
img {
  border: 0;
  max-width: 100%!important;
  vertical-align: middle;
}
address,
caption,
cite,
code,
dfn,
i,
em,
strong,
th,
var {
  font-weight: normal;
  font-style: normal;
}
ol,
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover,
a:focus {
  outline: none;
}
caption,
th {
  text-align: left;
  font-weight: normal;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  font-size: 100%;
}
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
}
button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font: inherit;
  color: inherit;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box
}
:before,
:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section,
summary {
  display: block;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.mui-flex {
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}
.mui-flex,
.mui-flex *,
.mui-flex :after,
.mui-flex :before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.mui-flex > .cell {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  width: 0;
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  max-width: 100%;
  display: block;
  padding: 0 !important;
  position: relative;
}
.mui-flex > .cell.fixed {
  -webkit-box-flex: 0 !important;
  -ms-flex: none !important;
  flex: none !important;
  width: auto;
}
.fl{
  float: left;
}
.fr{
  float: right;
}
.a-line{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.t-line{
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}
.text-center{
  text-align: center;
}
.index-clear {
    width: 100%;
    height: 10px;
    background-color: #eee;
}
.mint-header{
